import React, { useState } from "react";
import { connect } from "react-redux";

import dataProvincia from "../data.json";

import registerUserActions from "../redux/actions/registerActions";

const Register = (props) => {
  
  const [state, setState] = useState({
    nombre: "",
    comercio: "",
    direccion: "",
    ciudad: "",
    telefono: "",
    provincia: "",
    radio1: "",
    radio2: "",
    correo: "",
    clave: "",
    estado: "solicitud",
    rol: "comprador",
  });

  const InputChange = e => {
    setState({
      ...state,
      [e.target.name]: e.target.value
    });
  };

  const SubmitData = e => {
    e.preventDefault();
    const user = {
      nombre: state.nombre,
      comercio: state.comercio,
      direccion: state.direccion,
      ciudad: state.ciudad,
      telefono: state.telefono,
      provincia: state.provincia,
      radio1: state.radio1,
      radio2: state.radio2,
      correo: state.correo,
      clave: state.clave,
      estado: state.estado,
      rol: state.rol
    };
    
    if (!user.nombre || !user.comercio || !user.direccion || !user.ciudad || !user.telefono || !user.provincia || !user.radio1 || !user.radio2 || !user.correo || !user.clave) {
      alert("Completar todos los campos obligatorios para poder registrarse")
    } else {     
      props.registerUsers(user)
    }
  
  }

  return (
      <div>
      <div className="uk-container">
      
      <div class="uk-grid-small uk-child-width-expand@s" uk-grid="true">
        <div>
          <h1 className="uk-text-center uk-padding">Registrarse</h1>
        </div>
      </div>
      { 
        props.array._id && 
        <div class="uk-grid-small uk-child-width-expand@s" uk-grid="true">
          <div>
            <div class="uk-alert-success" uk-alert="true">
                <p>El usuario se registro correctamente..</p>
            </div>
          </div>
        </div>
      } 

      <div class="uk-grid-small uk-child-width-expand@s" uk-grid="true">
          
          <div>
              <div class="uk-card uk-card-body">
                

                <form uk-grid="true">
                  <div>
                    <div className="uk-form-stacked uk-grid-small" uk-grid="true">
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Nombre Completo *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="nombre"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Nombre del Comercio *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="comercio"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Direcci√≥n *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="direccion"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Ciudad *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="ciudad"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Tel√©fono *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="telefono"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Provincia *</label>
                          <div className="uk-form-controls">
                            <select className="uk-select" name="provincia" onChange={InputChange}>
                                {
                                  dataProvincia.map((provincia, index) => {
                                    return(
                                      <option key={index} value={provincia.iso_nombre}>
                                        {provincia.iso_nombre}
                                      </option>
                                    )
                                  })
                                }
                            </select>
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Correo Electr√≥nico *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="correo"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="uk-width-1-2@s">
                        <div className="uk-margin">
                          <label className="uk-form-label" >Contrase√±a *</label>
                          <div className="uk-form-controls">
                            <input
                              className="uk-input"
                              type="text"
                              name="clave"
                              onChange={InputChange}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                </form>

            </div>
          </div>

          <div>
      
            <div className="uk-form-stacked uk-grid-small uk-margin-medium-top" uk-grid="true">
              <legend className="uk-legend">Seleccione rubro de su comercio:</legend>
              <div className="uk-margin uk-grid uk-child-width-auto">
                <label><input value="Venta articulos de limpieza" className="uk-radio" type="radio" name="radio1" checked={state.radio1 === 'Venta articulos de limpieza'} onChange={InputChange}/> Venta Art. de Limpieza</label>
                <label><input value="Mayorista articulos de limpieza" className="uk-radio" type="radio" name="radio1" checked={state.radio1 === 'Mayorista articulos de limpieza'} onChange={InputChange}/> Mayorista Art. de Limpieza</label>
              </div>
              <div className="uk-margin-small uk-grid uk-child-width-auto">
                <label><input value="Supermercado" className="uk-radio" type="radio" name="radio1" checked={state.radio1 === 'Supermercado'} onChange={InputChange}/> Supermercado</label>
                <label><input value="Otro rubro" className="uk-radio" type="radio" name="radio1" checked={state.radio1 === 'Otro rubro'} onChange={InputChange}/> Otro rubro</label>
              </div>
            </div>
            
            <hr className="uk-divider-small"></hr>
            
            <div className="uk-form-stacked uk-grid-small" uk-grid="true">
              <legend className="uk-legend">Solicite su numero de cliente para acceder a nuestro carro de compra, catalogo y lista de precios:</legend>
              <div className="uk-margin uk-grid uk-child-width-auto">
                <label><input value="Ya soy cliente" className="uk-radio" type="radio" name="radio2" checked={state.radio2 === 'Ya soy cliente'} onChange={InputChange}/> Ya soy cliente</label>
                <label><input value="Es mi primera compra" className="uk-radio" type="radio" name="radio2" checked={state.radio2 === 'Es mi primera compra'} onChange={InputChange}/> Es mi primera compra</label>
              </div>
            </div>
      
          </div>

      </div>


      <div className="uk-container">
        <div
          className="uk-grid-collapse uk-child-width-expand@s uk-grid"
          uk-grid="true"
          uk-height-viewport="expand: true"
        >
          <div className="uk-margin-auto-vertical">
            
            

            <div style={{marginTop: '60px'}}>
              
            </div>

            <div style={{marginTop: '20px'}}>
              <button type="button" className="uk-button uk-button-primary uk-align-right" onClick={SubmitData}>
                Registrarme
              </button>
            </div>

          </div>
        </div>
      </div>

    </div>
    </div>
  );
};

const mapDispatchToProps = dispatch => {
  return {
    registerUsers: (user) => { dispatch(registerUserActions(user)) }
  };
};

const mapStateToProps = (state) => {
  return {
    loggedIn: state.register.loggedIn,
    fetching: state.register.isFetching,
    array: state.register.array,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Register);
